@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&family=Noto+Serif+JP:wght@200;300;400;500;600;700;900&family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  margin: 0;
  font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.contactForm{
padding:30px;
font-family:'Work Sans', sans-serif;
}
input {
  box-sizing: border-box;
  width: 100%;
  margin-bottom:20px;
  padding:10px;
  border-radius:5px;
  border:1px solid #b9b9b9;
  font-size:15px;
  color:#383838;
  margin-top:8px;
  background-color: #f7f7f7;
}
textarea{
  box-sizing: border-box;
  width: 100%;
  margin-bottom:20px;
  height:200px;
  padding:10px;
  border-radius:5px;
  border:1px solid #b9b9b9;
  margin-top:8px;
  background-color: #f7f7f7;
}
textarea:focus, input:focus{
  outline-color: orange;
  background-color:rgb(253, 246, 232)
}
.submitBtn{
  display: block;
  font-family: 'Work Sans', sans-serif;
  color: #046541;
  letter-spacing: .1em;
  font-weight: 600;
  width: 100%;
  border: none;
  background-color: #00e59b;
  padding: 14px 28px;
  font-size: 1em;
  margin: auto;
  cursor: pointer;
  text-align: center;
  border-radius: 10px;
}
.projectImage{
  object-fit: cover;
  opacity: 0.4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Typewriter {
  display: inline;
}
.typewriterCursor{
  color:rgb(189, 189, 189);
  -webkit-animation: blink-1 1.7s 1s infinite both;
  animation: blink-1 1.7s 1s infinite both;
}
.typewriterWrapper{
  color:#ffb80b;
}
.fade-in {
	-webkit-animation: fade-in 1.5s cubic-bezier(0.895, 0.030, 0.685, 0.220) both;
	        animation: fade-in 1.5s cubic-bezier(0.895, 0.030, 0.685, 0.220) both;
}
/**
 * ----------------------------------------
 * animation fade-in
 * ----------------------------------------
 */
@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fade-in-bottom {
	-webkit-animation: fade-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: fade-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
/**
 * ----------------------------------------
 * animation fade-in-bottom
 * ----------------------------------------
 */
 @-webkit-keyframes fade-in-bottom {
  0% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fade-in-bottom {
  0% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@-webkit-keyframes blink-1 {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}
@keyframes blink-1 {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

